import React from 'react'
import { useSelector } from 'react-redux'

import ListHeader from './ListHeader'
import ListFooter from './ListFooter'
import ListCard from './ListCard'

function List({ id, title, index }) {
  const numOfListCards = useSelector((state) => state.listCard.numOfListCards);
  const listId = id;

  // let currentDroppable = null;

  const onMouseMove = (event) => {
    let elemBelow = document.elementFromPoint(event.clientX, event.clientY);
    console.log(event.clientX, event.clientY);

    if (elemBelow.closest('.list')) {
       console.log('yes', index);
    }


    // if (!elemBelow) return;

    // let droppableBelow = elemBelow.closest('.list');

    // if (currentDroppable !== droppableBelow) {
    //   if (currentDroppable) {
    //     console.log('leaveDroppable');
    //   }
    //   currentDroppable = droppableBelow;
    //   if (currentDroppable) {
    //     console.log('enterDroppable');
    //   }
    // }

  }

  document.addEventListener('mousemove', onMouseMove);

  const onDragOver = (event) => {
    event.preventDefault();
    console.log('onDragOver Init')
  };

  const onDrop = (event, cat) => {
    let id = event.dataTransfer.getData('id');

    console.log('onDrop: ',{ id });

    // let tasks = this.state.tasks.filter((task) => {
    //   if (task.name == id) {
    //     task.category = cat;
    //   }
    //   return task;
    // });

    // this.setState({
    //   ...this.state,
    //   tasks,
    // });
  };

  return (
    <div
      className='list'
      onDragOver={(event) => onDragOver(event)}
      onDrop={(event) => {
        onDrop(event, id);
      }}
    >
      <ListHeader id={id} title={title} index={index} />
      {numOfListCards.map((listCard) =>
        listCard.listId === listId ? (
          <ListCard key={listCard.id} id={listCard.id} title={listCard.title} />
        ) : (
          ''
        )
      )}
      <ListFooter id={id} />
    </div>
  );
}

export default List
