import React, { useState } from 'react'
import { useDispatch } from 'react-redux';
import { deleteListCard, updateListCard } from '../../redux';

function ListCard({ id, title, index }) {
  const [listCardTitle, setListCardTitle] = useState('');
  const [showCardTitle, setShowCardTitle] = useState(false);
  const dispatch = useDispatch();

  const updateListCardTitle = () => {
    const listCardTitleValue = listCardTitle ? listCardTitle : 'New task';
    setShowCardTitle(true);
    dispatch(updateListCard(index, id, listCardTitleValue));
  };

  const handleChange = (event) => {
    setListCardTitle(event.target.value);
  };

  const handleBlur = () => {
    updateListCardTitle();
  };

  const handleEnter = (event) => {
    if (event.key === 'Enter') {
      updateListCardTitle();
    }
  };

  const editTitle = () => {
    setShowCardTitle(false);
  };

  let activeItem = null;
  let active = false;

  const onDragStart = (event, id) => {
    event.dataTransfer.setData('id', id);

    active = true;
    console.log('onDragStart', active);

    // this is the item we are interacting with
    activeItem = event.target;

    if (activeItem !== null) {
      if (!activeItem.xOffset) {
        activeItem.xOffset = 0;
      }

      if (!activeItem.yOffset) {
        activeItem.yOffset = 0;
      }

      if (event.type === 'touchstart') {
        activeItem.initialX = event.touches[0].clientX - activeItem.xOffset;
        activeItem.initialY = event.touches[0].clientY - activeItem.yOffset;
      } else {
        console.log('doing something!');
        activeItem.initialX = event.clientX - activeItem.xOffset;
        activeItem.initialY = event.clientY - activeItem.yOffset;
      }
    }
  };

  const dragEnd = event => {
    console.log("dragEnd")
    if (activeItem !== null) {
      activeItem.classList.remove('dragging');
      activeItem.style.transform = 'translate3d(' + activeItem.currentX + 'px, ' + activeItem.currentY + 'px, 0) rotate(0deg)';
    }

    active = false;
    activeItem = null;
  }

  const drag = (event) => {
    event.preventDefault();
    if (active) {
      console.log('drag', active);
      if (event.type === 'touchmove') {
        event.preventDefault();

        activeItem.currentX = event.touches[0].clientX - activeItem.initialX;
        activeItem.currentY = event.touches[0].clientY - activeItem.initialY;
      } else {
        activeItem.currentX = event.clientX - activeItem.initialX;
        activeItem.currentY = event.clientY - activeItem.initialY;
      }

      activeItem.xOffset = activeItem.currentX;
      activeItem.yOffset = activeItem.currentY;

      console.log(activeItem.xOffset, activeItem.yOffset);

      activeItem.classList.add('dragging');
      activeItem.style.transform =
        'translate3d(' +
        activeItem.currentX +
        'px, ' +
        activeItem.currentY +
        'px, 0) rotate(3deg)';
    }
  };

  return (
    <div
      className='list-card'
      draggable
      onDragStart={(event) => onDragStart(event, id)}
      onDrag={(event) => drag(event)}
      onDragEnd={(event) => dragEnd(event)}
    >
      {showCardTitle ? (
        <div className='list-card-task' onClick={() => editTitle()}>
          {title}
        </div>
      ) : (
        <input
          autoFocus
          type='text'
          value={listCardTitle}
          onChange={(event) => handleChange(event)}
          onKeyPress={(event) => handleEnter(event)}
          onBlur={() => handleBlur()}
          placeholder='Add task'
        />
      )}
      <button
        className='btn-delete-card'
        onClick={() => dispatch(deleteListCard(id))}
      >
        &times;
      </button>
    </div>
  );
}

export default ListCard
